import { array, object, string, mixed, number } from 'yup';
import { validate, noErrors } from 'statelib/validate';

export const addressValidationSchema = mixed()
  .defined()
  .test(
    (item) =>
      typeof item === 'object' &&
      noErrors(
        object({
          addressLineOne: string()
            .trim()
            .required('address line one must be specified'),
          postcode: string().trim().required('postcode must be specified'),
          city: string().trim().required('city must be specified'),
        }),
        item,
      ),
  )
  .label('address');

export const movingInDateValidationSchema = object({
  month: number().min(1).max(31),
  year: number().min(1900).max(new Date().getFullYear()),
});

export const userAddressValidationSchema = object({
  selectedAddress: addressValidationSchema,
  accommodationCircumstances: string()
    .defined()
    .label('accommodation circumstances'),
  movingInDate: mixed()
    .defined()
    .test(
      (item) =>
        typeof item !== 'object' ||
        noErrors(movingInDateValidationSchema, item),
    )
    .label('moving in date'),
});

const primaryTelNumberSchema = object({
  primaryTelNumber: string()
    .trim()
    .required('mobile number must be specified')
    .min(6, 'mobile number must be specified')
    .max(17, 'mobile number is too long')
    .test(
      'primaryTelNumber',
      'UK mobile number must start with "07" and be 11 digits long',
      (s) =>
        !s ||
        !s.startsWith('+44') ||
        noErrors(
          string()
            .defined()
            .matches(/^(07\d{9})$/),
          s.replace('+440', '0').replace('+44', '0'),
        ),
    )
    .label('mobile number'),
});

const addressesSchema = object({
  addresses: array()
    .min(1)
    .of(userAddressValidationSchema)
    .test(
      (addresses) =>
        !!addresses &&
        addresses[addresses.length - 1]!.movingInDate === 'moreThan3Years',
    ),
});

export const aboutYouContactValidation = validate(primaryTelNumberSchema);

export const aboutYouAddressesValidation = validate(addressesSchema);

export const contactDetailsValidation = validate(
  primaryTelNumberSchema.concat(addressesSchema),
);
