import { PCAProductCode } from 'services/customer';
import { OnboardingContext } from './context';

export const productCodeToContext = (
  code: PCAProductCode | null,
): OnboardingContext['productCode'] | undefined => {
  if (!code) return undefined;
  switch (code) {
    case PCAProductCode.basic:
      return 'BASIC_CA';
    case PCAProductCode.choice:
      return 'CHOICE_CA';
    case PCAProductCode.everyday:
      return 'EVERYDAY_CA';
    case PCAProductCode.lite123:
      return '123_LITE_CA';
    case PCAProductCode.olb123:
      return '123_CURRENT_ACC_OLB';
    case PCAProductCode.select:
      return 'SELECT_CA';
    default:
      return undefined;
  }
};

export const contextToProductCode = (
  code: OnboardingContext['productCode'] | undefined,
): PCAProductCode | null => {
  if (!code) return null;
  switch (code) {
    case 'BASIC_CA':
      return PCAProductCode.basic;
    case 'CHOICE_CA':
      return PCAProductCode.choice;
    case 'EVERYDAY_CA':
      return PCAProductCode.everyday;
    case '123_LITE_CA':
      return PCAProductCode.lite123;
    case '123_CURRENT_ACC_OLB':
      return PCAProductCode.olb123;
    case 'SELECT_CA':
      return PCAProductCode.select;
    default:
      return null;
  }
};
