import { PurposeOfAccount } from 'services/customer';
import { OnboardingContext } from './context';

export const purposeOfAccountToContext = (
  purpose: PurposeOfAccount | null,
): OnboardingContext['account']['purposeOfNewAccount'] => {
  if (!purpose) return undefined;
  switch (purpose) {
    case PurposeOfAccount.mainBankAccount:
      return 'MAIN_BANK_ACCOUNT';
    case PurposeOfAccount.other:
      return 'OTHER';
    case PurposeOfAccount.savings:
      return 'SAVINGS';
    case PurposeOfAccount.secondaryBankAccount:
      return 'SECONDARY_BANK_ACCOUNT';
    case PurposeOfAccount.tier1UkVisa:
      return 'TIER1_UK_VISA';
    default:
      return undefined;
  }
};

export const contextToPurposeOfAccount = (
  purpose: OnboardingContext['account']['purposeOfNewAccount'],
): PurposeOfAccount | null => {
  if (!purpose) return null;
  switch (purpose) {
    case 'MAIN_BANK_ACCOUNT':
      return PurposeOfAccount.mainBankAccount;
    case 'OTHER':
      return PurposeOfAccount.other;
    case 'SAVINGS':
      return PurposeOfAccount.savings;
    case 'SECONDARY_BANK_ACCOUNT':
      return PurposeOfAccount.secondaryBankAccount;
    case 'TIER1_UK_VISA':
      return PurposeOfAccount.tier1UkVisa;
    default:
      return null;
  }
};
