import Page from 'components/Page/Page';
import ProgressBar from 'components/ProgressBar/ProgressBar';
import Heading, { SecondaryHeading } from 'components/Text/Heading';
import { useOnboarding } from 'lib/useOnboarding';
import PrimaryButton from 'components/Button/PrimaryButton';
import Label from 'components/Form/Label';
import Selection from 'components/Form/Selection';
import { stateMatches } from 'statelib/stateMatches';
import PersonUpdateError from 'components/PersonUpdateError/PersonUpdateError';
import FormLink from 'components/Form/FormLink';
import {
  financesValidation,
  OnboardingContext,
} from 'sanstatecharts-onboarding';
import MoneyInput from 'components/Form/MoneyInput';
import ModalLink from 'components/Modals/ModalLink';
import Modal from 'components/Modals/Modal';
import OutgoingsAreIncreasing from 'modals/OutgoingsAreIncreasing';
import Radios from 'components/Form/Radios';
import StandardInput from 'components/Form/StandardInput';
import DateInput from 'components/Form/DateInput';
import OccupationInput from 'components/Form/OccupationInput';
import ValidationErrors from 'components/ValidationErrors/ValidationErrors';
import { useValidation } from 'lib/useValidation';
import { fieldError } from 'components/ValidationErrors/findError';
import Form from 'components/Form/Form';
import IndustrySectorInput from 'components/Form/IndustrySectorInput';
import FundsInput from 'components/Form/FundsInput';
import IncomeIsReducing from 'modals/IncomeIsReducing';
import styled from 'styled-components';

const IncomeSourceContainer = styled.div``;

export default function Finances() {
  const [state, send, canTransition] = useOnboarding();
  const { finances } = state.context;
  const disabled = !canTransition({
    type: 'UPDATE_FINANCES',
    field: 'employmentStatus',
    value: 'EMPLOYED',
  });

  const validationErrors = financesValidation(finances);
  const { shownErrors, refFor, refCreate, onScrollToError } = useValidation(
    validationErrors,
  );

  return (
    <Page>
      <ProgressBar step="finances" title="Your finances" />
      <Heading>Employment</Heading>
      <Form>
        <Label
          error={shownErrors['employmentStatus']}
          ref={refFor('employmentStatus')}>
          Employment status
          <Selection
            name="employmentStatus"
            placeholder="Please select..."
            options={{
              EMPLOYED: 'Employed',
              HOMEMAKER: 'Homemaker',
              RETIRED: 'Retired',
              SELF_EMPLOYED: 'Self employed',
              STUDENT: 'Student',
              UNEMPLOYED: 'Unemployed',
            }}
            onChange={(value) =>
              send({
                type: 'UPDATE_FINANCES',
                field: 'employmentStatus',
                value:
                  (value as OnboardingContext['finances']['employmentStatus']) ||
                  undefined,
              })
            }
            error={shownErrors['employmentStatus']}
            disabled={disabled}
            value={finances.employmentStatus}
          />
        </Label>
        {(finances.employmentStatus === 'EMPLOYED' ||
          finances.employmentStatus === 'SELF_EMPLOYED') && (
          <>
            <Label error={shownErrors['occupation']} ref={refFor('occupation')}>
              {finances.employmentStatus === 'EMPLOYED'
                ? 'Occupation'
                : 'Position in your business'}
              <OccupationInput
                onChange={(value) =>
                  send({
                    type: 'UPDATE_FINANCES',
                    field: 'occupation',
                    value,
                  })
                }
                error={shownErrors['occupation']}
                disabled={disabled}
                value={finances.occupation}
              />
            </Label>
            <Label
              error={shownErrors['employmentStarted']}
              ref={refFor('employmentStarted')}>
              {finances.employmentStatus === 'EMPLOYED'
                ? 'Date started'
                : 'Date you started your business'}
              <DateInput
                onChange={(value) =>
                  send({
                    type: 'UPDATE_FINANCES',
                    field: 'employmentStarted',
                    value,
                  })
                }
                error={shownErrors['employmentStarted']}
                disabled={disabled}
                value={finances.employmentStarted || ''}
              />
            </Label>
            <Label
              error={shownErrors['companyName']}
              ref={refFor('companyName')}>
              {finances.employmentStatus === 'EMPLOYED'
                ? 'Company name'
                : 'Name of your business'}
              <StandardInput
                placeholder="Enter company name"
                error={shownErrors['companyName']}
                disabled={disabled}
                value={finances.companyName}
                onChange={(value) =>
                  send({
                    type: 'UPDATE_FINANCES',
                    field: 'companyName',
                    value,
                  })
                }
              />
            </Label>
          </>
        )}
        {finances.employmentStatus === 'EMPLOYED' && (
          <Label
            error={shownErrors['companyControl']}
            ref={refFor('companyControl')}>
            Do you have control over the company you are employed by?
            <Radios
              name="companyControl"
              options={['Yes', 'No']}
              onChange={(companyControl) =>
                send({
                  type: 'UPDATE_FINANCES',
                  field: 'companyControl',
                  value: companyControl === 'Yes',
                })
              }
              error={shownErrors['companyControl']}
              disabled={disabled}
              value={
                finances.companyControl === undefined
                  ? undefined
                  : finances.companyControl
                  ? 'Yes'
                  : 'No'
              }
            />
          </Label>
        )}
        {(finances.employmentStatus === 'SELF_EMPLOYED' ||
          (finances.employmentStatus === 'EMPLOYED' &&
            finances.companyControl)) && (
          <Label
            error={shownErrors['industrySector']}
            ref={refFor('industrySector')}>
            Industry sector
            <IndustrySectorInput
              onChange={(value) =>
                send({
                  type: 'UPDATE_FINANCES',
                  field: 'industrySector',
                  value,
                })
              }
              error={shownErrors['industrySector']}
              disabled={disabled}
              value={finances.industrySector}
            />
          </Label>
        )}
        <SecondaryHeading>Income</SecondaryHeading>
        {finances.incomeSources.map((incomeSource, index) => (
          <IncomeSourceContainer key={`incomeSource${index}`}>
            <FundsInput
              title="Income source"
              amountTitle="Income from this source"
              originTitle="Is the income from the UK?"
              name={`incomeSource${index}`}
              value={incomeSource}
              error={fieldError(shownErrors, 'incomeSources', index)}
              refFor={refCreate('incomeSources', index)}
              onChange={(value) =>
                send({ type: 'UPDATE_INCOME_SOURCE', index, value })
              }>
              <ModalLink
                onClick={() => send({ type: 'INCOME_IS_REDUCING' })}
                disabled={!canTransition({ type: 'INCOME_IS_REDUCING' })}>
                What if my income is reducing?
              </ModalLink>
              <Modal
                enabled={stateMatches(
                  state,
                  'steps',
                  'finances',
                  'incomeIsReducing',
                )}>
                <IncomeIsReducing />
              </Modal>
            </FundsInput>
            {canTransition({ type: 'REMOVE_INCOME_SOURCE', index }) && (
              <FormLink
                inline
                onClick={() => send({ type: 'REMOVE_INCOME_SOURCE', index })}>
                Remove
              </FormLink>
            )}
          </IncomeSourceContainer>
        ))}
        {canTransition({ type: 'ADD_INCOME_SOURCE' }) && (
          <FormLink onClick={() => send({ type: 'ADD_INCOME_SOURCE' })}>
            Add another income source
          </FormLink>
        )}
        <SecondaryHeading>Outgoings</SecondaryHeading>
        <Label error={shownErrors['outgoings']} ref={refFor('outgoings')}>
          Your monthly outgoings
          <MoneyInput
            onChange={(outgoings) =>
              send({
                type: 'UPDATE_FINANCES',
                field: 'outgoings',
                value: outgoings,
              })
            }
            error={shownErrors['outgoings']}
            disabled={disabled}
            value={finances.outgoings}
          />
        </Label>
        <ModalLink
          onClick={() => send({ type: 'OUTGOINGS_ARE_INCREASING' })}
          disabled={!canTransition({ type: 'OUTGOINGS_ARE_INCREASING' })}>
          What if my outgoings are increasing?
        </ModalLink>
        <Modal
          enabled={stateMatches(
            state,
            'steps',
            'finances',
            'outgoingsAreIncreasing',
          )}>
          <OutgoingsAreIncreasing />
        </Modal>
        <Label error={shownErrors['dependents']} ref={refFor('dependents')}>
          Financial dependents
          <Radios
            name="dependents"
            options={['0', '1', '2', '3']}
            onChange={(dependents) =>
              send({
                type: 'UPDATE_FINANCES',
                field: 'dependents',
                value: parseInt(
                  dependents,
                  10,
                ) as OnboardingContext['finances']['dependents'],
              })
            }
            value={
              finances.dependents === undefined
                ? undefined
                : finances.dependents.toString()
            }
            error={shownErrors['dependents']}
          />
        </Label>
        <PersonUpdateError
          enabled={stateMatches(state, 'steps', 'finances', 'updateError')}
        />
        <ValidationErrors
          validationErrors={validationErrors}
          onScrollToError={onScrollToError}
        />
        <PrimaryButton
          type="submit"
          onClick={() => send({ type: 'CONTINUE' })}
          status={
            stateMatches(state, 'steps', 'finances', 'updating')
              ? 'SUBMITTING'
              : !canTransition({ type: 'CONTINUE' })
              ? 'DISABLED'
              : 'ACTIVE'
          }>
          Continue
        </PrimaryButton>
      </Form>
    </Page>
  );
}
