import { array, mixed, object, string, number, boolean } from 'yup';
import { EmploymentTuple } from 'services/customer';
import { validate } from 'statelib/validate';
import { dateStringRegex } from './dateStringRegex';

export const incomeSourceValidationSchema = object({
  type: string()
    .trim()
    .required('income source must be specified')
    .label('source'),
  amount: number().defined().min(1).label('income'),
  origin: string()
    .trim()
    .required('income country must be specified')
    .label('income country'),
}).label('income source');

export const financesValidation = validate(
  object({
    employmentStatus: string()
      .trim()
      .required('employment status must be specified')
      .label('employment status'),
    occupation: mixed().when('employmentStatus', {
      is: (s: string) => s === 'EMPLOYED' || s === 'SELF_EMPLOYED',
      then: mixed().test(
        'occupation',
        'please search for the closest matching occupation',
        (o: string | EmploymentTuple) => typeof o !== 'string',
      ),
    }),
    employmentStarted: string().when('employmentStatus', {
      is: (s: string) => s === 'EMPLOYED' || s === 'SELF_EMPLOYED',
      then: string()
        .trim()
        .required()
        .matches(dateStringRegex, 'employment start date is invalid')
        .label('employment start date'),
    }),
    companyName: string()
      .when('employmentStatus', {
        is: (s: string) => s === 'EMPLOYED' || s === 'SELF_EMPLOYED',
        then: string().trim().required('company name must be specified'),
      })
      .label('company name'),
    companyControl: boolean()
      .when('employmentStatus', {
        is: 'EMPLOYED',
        then: boolean().defined(),
        otherwise: boolean().nullable(),
      })
      .label('control of your company'),
    industrySector: mixed()
      .when(['employmentStatus', 'companyControl'], {
        is: (s: string, b: boolean | undefined) => s === 'SELF_EMPLOYED' || b,
        then: mixed().test(
          'occupation',
          'please search for the closest matching industry sector',
          (o: string | EmploymentTuple) => typeof o !== 'string',
        ),
        otherwise: string(),
      })
      .label('industry sector'),
    incomeSources: array()
      .min(1)
      .of(incomeSourceValidationSchema)
      .label('income sources'),
    outgoings: number().defined(),
    dependents: number().defined(),
  }),
);
