import { Machine } from 'xstate';
import { updatePerson } from 'services/customer';
import { createAddress } from 'services/address';
import { noErrors } from 'statelib/validate';
import { addressResponseToAddress, UserAddress } from './address';
import {
  aboutYouToPersonUpdate,
  accountToPersonUpdate,
  contactDetailsToPersonUpdate,
  financesToPersonUpdate,
  overdraftToPersonUpdate,
} from './person';
import { OnboardingContext } from './context';
import { magicLinkMachine } from './magicLink.machine';
import { userAddressValidationSchema } from './validation/contactDetailsValidation';

const addressWithIdPromise = (userId: string) => (
  address: UserAddress,
): Promise<UserAddress> => {
  if (
    typeof address.selectedAddress === 'string' ||
    address.selectedAddress.id ||
    !noErrors(userAddressValidationSchema, address)
  ) {
    return Promise.resolve(address);
  }
  return createAddress({
    address: {
      address_line_1: address.selectedAddress.addressLineOne,
      address_line_2: address.selectedAddress.addressLineTwo,
      address_line_3: address.selectedAddress.addressLineThree,
      city: address.selectedAddress.city,
      postal_code: address.selectedAddress.postcode,
      country_code: address.selectedAddress.country,
      district_or_county: '',
      state_or_region: '',
    },
    addressableId: userId,
    userId,
    addressableType: 'CUSTOMER',
    type: 'RESIDENTIAL',
  }).then(({ data }) => ({
    ...address,
    selectedAddress: addressResponseToAddress(data),
  }));
};

export const services = {
  magicLink: <Context extends OnboardingContext>({ auth }: Context) =>
    Machine(magicLinkMachine).withContext({
      auth,
      emailAddress: '',
    }),
  updatePersonAboutYou: <Context extends OnboardingContext>({
    auth,
    aboutYou,
  }: Context) =>
    updatePerson({
      userId: auth.userId,
      updatePayload: aboutYouToPersonUpdate(aboutYou),
    }).then(({ status }) => {
      if (status !== 204) {
        throw new Error(`person update response status ${status}`);
      }
    }),
  updatePersonContactDetails: async <Context extends OnboardingContext>({
    auth,
    contactDetails,
  }: Context) => {
    const updatedAddresses = await Promise.all(
      contactDetails.addresses.map(addressWithIdPromise(auth.userId)),
    );
    const { status } = await updatePerson({
      userId: auth.userId,
      updatePayload: contactDetailsToPersonUpdate({
        ...contactDetails,
        addresses: updatedAddresses,
      }),
    });
    if (status !== 204) {
      throw new Error(`person update response status ${status}`);
    }
    return updatedAddresses;
  },
  updatePersonFinances: <Context extends OnboardingContext>({
    auth,
    finances,
  }: Context) =>
    updatePerson({
      userId: auth.userId,
      updatePayload: financesToPersonUpdate(finances),
    }).then(({ status }) => {
      if (status !== 204) {
        throw new Error(`person update response status ${status}`);
      }
    }),
  updatePersonAccount: <Context extends OnboardingContext>({
    auth,
    account,
  }: Context) =>
    updatePerson({
      userId: auth.userId,
      updatePayload: accountToPersonUpdate(account),
    }).then(({ status }) => {
      if (status !== 204) {
        throw new Error(`person update response status ${status}`);
      }
    }),
  updatePersonOverdraft: <Context extends OnboardingContext>({
    auth,
    overdraft,
  }: Context) =>
    updatePerson({
      userId: auth.userId,
      updatePayload: overdraftToPersonUpdate(overdraft),
    }).then(({ status }) => {
      if (status !== 204) {
        throw new Error(`person update response status ${status}`);
      }
    }),
};
