import { Errors, tinMandatory, tinOptional } from 'sanstatecharts-onboarding';
import Label from 'components/Form/Label';
import { OnboardingContext } from 'machines/context';
import { OnboardingEvent } from 'machines/events';
import { RefFor } from 'lib/useValidation';
import CountryInput from 'components/Form/CountryInput';
import { findError } from 'components/ValidationErrors/findError';
import FormLink from 'components/Form/FormLink';
import Radios from 'components/Form/Radios';
import { countriesData } from 'lib/countries';
import StandardInput from 'components/Form/StandardInput';

type Values = Pick<
  OnboardingContext['aboutYou'],
  'taxCountries' | 'taxIdNumber'
>;

type Props = {
  shownErrors: Errors<Values>;
  refFor: RefFor<Values>;
  send: (e: OnboardingEvent) => void;
  canTransition: (e: OnboardingEvent) => boolean;
  aboutYou: Values;
};

export default function TaxStatusFields({
  shownErrors,
  aboutYou,
  send,
  canTransition,
  refFor,
}: Props) {
  const disabled =
    !canTransition({
      type: 'UPDATE_TAX_COUNTRY',
      index: 0,
      value: 'COUNTRY',
    }) &&
    !canTransition({
      type: 'ADD_TAX_COUNTRY',
    });
  return (
    <>
      <Label error={shownErrors['taxCountries']} ref={refFor('taxCountries')}>
        Are you obliged to submit a personal tax return in another country
        because of residence or citizenship?
        <Radios
          name="taxCountriesForeign"
          options={['Yes', 'No']}
          onChange={(value) => {
            if (aboutYou.taxCountries.length === 0) {
              send({ type: 'ADD_TAX_COUNTRY' });
            }
            if (value === 'No') {
              if (aboutYou.taxCountries.length > 1) {
                Array.from({
                  length: aboutYou.taxCountries.length - 1,
                }).forEach(() =>
                  send({ type: 'REMOVE_TAX_COUNTRY', index: 1 }),
                );
              }
              send({
                type: 'UPDATE_TAX_COUNTRY',
                index: 0,
                value: countriesData[0]!.ISOcode,
              });
            } else {
              send({ type: 'UPDATE_TAX_COUNTRY', index: 0, value: '' });
            }
          }}
          disabled={disabled}
          value={
            aboutYou.taxCountries.length === 0
              ? undefined
              : aboutYou.taxCountries.length === 1 &&
                aboutYou.taxCountries[0] === countriesData[0]!.ISOcode
              ? 'No'
              : 'Yes'
          }
          error={shownErrors['taxCountries']}
        />
      </Label>
      {(aboutYou.taxCountries.length > 1 ||
        (aboutYou.taxCountries.length === 1 &&
          aboutYou.taxCountries[0] !== countriesData[0]!.ISOcode)) && (
        <>
          <Label
            error={shownErrors['taxCountries']}
            ref={refFor('taxCountries', 0)}>
            Country of tax origin
            {aboutYou.taxCountries.map((taxCountry, index) => (
              <CountryInput
                key={`taxCountry${index}`}
                name={`taxCountry${index}`}
                placeholder={
                  index === aboutYou.taxCountries.length - 1
                    ? 'Please select...'
                    : undefined
                }
                onChange={(value) =>
                  send({
                    type: 'UPDATE_TAX_COUNTRY',
                    index,
                    value,
                  })
                }
                disabled={disabled}
                value={taxCountry}
                error={findError(shownErrors, 'taxCountries', index)}
                ref={index !== 0 ? refFor('taxCountries', index) : undefined}
                filterOut={[
                  countriesData[0]!.ISOcode,
                  ...aboutYou.taxCountries,
                ]}>
                {canTransition({ type: 'REMOVE_TAX_COUNTRY', index }) && (
                  <FormLink
                    inline
                    onClick={() => send({ type: 'REMOVE_TAX_COUNTRY', index })}>
                    Remove
                  </FormLink>
                )}
              </CountryInput>
            ))}
          </Label>
          {canTransition({ type: 'ADD_TAX_COUNTRY' }) && (
            <FormLink onClick={() => send({ type: 'ADD_TAX_COUNTRY' })}>
              Add another
            </FormLink>
          )}
        </>
      )}
      {(tinMandatory(aboutYou.taxCountries) ||
        tinOptional(aboutYou.taxCountries)) && (
        <Label error={shownErrors['taxIdNumber']}>
          Taxpayer Identification Number (TIN)
          <StandardInput
            id="taxpayerIdentificationNumber"
            placeholder="Enter your Taxpayer Identification Number (TIN)"
            onChange={(value) =>
              send({ type: 'UPDATE_ABOUT_YOU', field: 'taxIdNumber', value })
            }
            disabled={disabled}
            value={aboutYou.taxIdNumber}
            error={shownErrors['taxIdNumber']}
            maxLength={20}
          />
        </Label>
      )}
    </>
  );
}
