import React from 'react';
import styled from 'styled-components';
import {
  Errors,
  noErrors,
  UserAddress,
  userAddressValidationSchema,
} from 'sanstatecharts-onboarding';
import { OnboardingContext } from 'machines/context';
import { OnboardingEvent } from 'machines/events';
import { RefCreate } from 'lib/useValidation';
import UserAddressInput from 'components/Form/UserAddressInput';
import FormLink from 'components/Form/FormLink';
import { Address } from 'sanstatecharts-onboarding';
import InfoBlock from 'components/InfoBlock/InfoBlock';
import BodyText from 'components/Text/BodyText';
import { ReactComponent as InfoIcon } from 'assets/info.svg';
import { fieldError } from 'components/ValidationErrors/findError';

const StyledInfoContent = styled.div`
  display: flex;
  flex-direction: row;
  align-items: center;
`;

const IconWrap = styled.div`
  position: relative;
  top: ${({ theme: { spacing } }) => spacing.s2};
  margin-right: ${({ theme: { spacing } }) => spacing.s4};
`;

const addressName = (a: Address | string) =>
  typeof a === 'string' ? a : a.addressLineOne || a.postcode;

type Values = Pick<OnboardingContext['contactDetails'], 'addresses'>;

type Props = {
  shownErrors: Errors<Values>;
  refCreate: RefCreate<Values>;
  send: (e: OnboardingEvent) => void;
  canTransition: (e: OnboardingEvent) => boolean;
  contactDetails: Values;
};

const prevIsComplete = (
  _userAddress: UserAddress,
  index: number,
  userAddresses: UserAddress[],
) =>
  index === 0 ||
  noErrors(userAddressValidationSchema, userAddresses[index - 1]);

export default function AddressFields({
  shownErrors,
  contactDetails,
  send,
  canTransition,
  refCreate,
}: Props) {
  return (
    <>
      {contactDetails.addresses.map(
        (userAddress, index) =>
          prevIsComplete(userAddress, index, contactDetails.addresses) && (
            <React.Fragment key={`address${index}`}>
              {index === 1 && (
                <InfoBlock>
                  <StyledInfoContent>
                    <IconWrap>
                      <InfoIcon />
                    </IconWrap>
                    <BodyText>
                      We'll need your address history for the last 3 years.
                    </BodyText>
                  </StyledInfoContent>
                </InfoBlock>
              )}
              <UserAddressInput
                name={`address${index}`}
                value={userAddress}
                title={
                  index === 0
                    ? 'Address'
                    : `Where did you live before ${addressName(
                        contactDetails.addresses[index - 1]!.selectedAddress,
                      )}?`
                }
                onChange={(value) =>
                  send({ type: 'UPDATE_ADDRESS', index, value })
                }
                error={fieldError(shownErrors, 'addresses', index)}
                refFor={refCreate('addresses', index)}>
                {canTransition({ type: 'REMOVE_ADDRESS', index }) && (
                  <FormLink
                    inline
                    onClick={() => send({ type: 'REMOVE_ADDRESS', index })}>
                    Remove
                  </FormLink>
                )}
              </UserAddressInput>
            </React.Fragment>
          ),
      )}
    </>
  );
}
