import { array, object, string, boolean } from 'yup';
import { validate } from 'statelib/validate';
import { dateStringRegex } from './dateStringRegex';

const nationalityValidationSchema = string()
  .trim()
  .required('nationality should be specified')
  .label('nationality');

const taxCountryValidationSchema = string()
  .trim()
  .required('tax residency country should be specified or removed')
  .label('tax country');

export const otherNameValidationSchema = object({
  name: string().trim().min(2).max(50).label('other name'),
  isMaidenName: boolean().defined(),
});

const mandatoryTinCountries = ['US'];
const optionalTinCountries = ['IN'];

const inList = (list: string[]) => (s: string) => list.includes(s);

export const tinMandatory = (taxCountries: string[]) =>
  taxCountries.some(inList(mandatoryTinCountries));

export const tinOptional = (taxCountries: string[]) =>
  taxCountries.some(inList(optionalTinCountries));

const uniqueEntries = <T>(value?: T[]) =>
  !value || value.every((v, i) => value.indexOf(v) === i);

const personalValidationSchema = object({
  salutation: string()
    .defined()
    .trim()
    .required('title must be specified')
    .label('title'),
  firstName: string()
    .trim()
    .min(2, 'first name must be specified')
    .max(50)
    .label('first name'),
  middleNames: string().trim().max(100).label('middle names'),
  lastName: string()
    .trim()
    .min(2, 'last name must be specified')
    .max(50)
    .label('last name'),
});

const additionalValidationSchema = object({
  otherNames: array()
    .defined()
    .max(5)
    .of(otherNameValidationSchema)
    .label('other names'),
  DOB: string()
    .trim()
    .required()
    .matches(dateStringRegex, 'date of birth is invalid')
    .label('date of birth'),
});

const nationalitiesValidationSchema = object({
  nationalities: array()
    .min(1)
    .of(nationalityValidationSchema)
    .test('unique', 'You must select unique nationalities', uniqueEntries)
    .label('nationalities'),
});

const taxStatusValidationSchema = object({
  taxCountries: array()
    .min(1, 'tax residency status must be specified')
    .of(taxCountryValidationSchema)
    .test(
      'unique',
      'You must select unique tax residency countries',
      uniqueEntries,
    )
    .label('tax residency status'),
  taxIdNumber: string()
    .when('taxCountries', (taxCountries: string[]) =>
      tinMandatory(taxCountries)
        ? string().defined().trim().min(9).max(20).label('Tax ID Number')
        : tinOptional(taxCountries)
        ? string().defined()
        : string().nullable(),
    )
    .label('Tax Identification Number'),
});

export const aboutYouPersonalValidation = validate(personalValidationSchema);

export const aboutYouAdditionalValidation = validate(
  additionalValidationSchema,
);
export const nationalitiesValidation = validate(nationalitiesValidationSchema);

export const taxStatusValidation = validate(taxStatusValidationSchema);

export const aboutYouValidation = validate(
  personalValidationSchema
    .concat(additionalValidationSchema)
    .concat(
      object({
        gender: string().trim().defined(),
      }),
    )
    .concat(nationalitiesValidationSchema)
    .concat(taxStatusValidationSchema),
);
