import { useContext, useEffect, useMemo } from 'react';
import {
  EventObject,
  Interpreter,
  MachineConfig,
  State as MachineState,
} from 'xstate';
import { useService } from '@xstate/react';
import { StateMachineContext } from './StateMachineContext';
import {
  assertIsDefinedInHook,
  findService,
  canTransition as canTransitionStatecharts,
  nextState as nextStateStatecharts,
  MachineContext,
  MachineSchema,
  MachineEvent,
} from 'sanstatecharts-onboarding';

function canTransitionToService<Context, Schema, Event extends EventObject>(
  service: Interpreter<Context, Schema, Event>,
) {
  return function canTransition(event: Event) {
    return canTransitionStatecharts(service, event);
  };
}

function nextStateForService<Context, Schema, Event extends EventObject>(
  service: Interpreter<Context, Schema, Event>,
) {
  return function nextState(event: Event) {
    return nextStateStatecharts(service, event);
  };
}

export function useStatechart<Machine extends MachineConfig<any, any, any>>(
  name: string,
) {
  type Context = MachineContext<Machine>;
  type Schema = MachineSchema<Machine>;
  type Event = MachineEvent<Machine>;
  const stateContext = useContext(StateMachineContext);
  assertIsDefinedInHook(stateContext, 'useStatechart', 'StateMachines');
  const [service, , recordedServices, setRecordedService] = stateContext;
  const foundService = findService(name, service);
  const recordedService = recordedServices[name] as
    | Interpreter<any>
    | undefined;
  const actor = foundService || recordedService;
  if (actor === undefined && (!stateContext || !stateContext[1])) {
    // eslint-disable-next-line no-console
    console.warn(`${service.id} has unknown consumed service: ${name}`);
  }
  const consumedService = ((actor || service) as unknown) as Interpreter<
    Context,
    Schema,
    Event
  >;

  const [state, send] = useService<Context, Event>(consumedService);
  const canTransition = useMemo(() => canTransitionToService(consumedService), [
    consumedService,
  ]);
  const nextState = useMemo(() => nextStateForService(consumedService), [
    consumedService,
  ]);
  if (foundService === undefined) {
    if (!state.done) {
      // eslint-disable-next-line no-console
      console.warn(`${service.id} consuming detached service: ${name}`);
    }
  }
  useEffect(() => {
    if (
      foundService !== undefined &&
      recordedServices[name] !== (consumedService as Interpreter<any, any, any>)
    ) {
      setRecordedService(name, consumedService);
    }
  }, [
    consumedService,
    foundService,
    name,
    recordedServices,
    setRecordedService,
  ]);
  return [
    (state as unknown) as MachineState<Context, Event, Schema>,
    send,
    canTransition,
    nextState,
  ] as [
    MachineState<Context, Event, Schema>,
    Interpreter<Context, Schema, Event>['send'],
    typeof canTransition,
    typeof nextState,
  ];
}

const doNothing = () => {};
const returnFalse = () => false;
const returnUndefined = () => undefined;

export function useStatechartIfExists<
  Machine extends MachineConfig<any, any, any>
>(name: string) {
  type Context = MachineContext<Machine>;
  type Schema = MachineSchema<Machine>;
  type Event = MachineEvent<Machine>;
  const stateContext = useContext(StateMachineContext);
  assertIsDefinedInHook(stateContext, 'useStatechartIfExists', 'StateMachines');
  const [service] = stateContext;
  const actor = findService(name, service);

  const consumedService = ((actor || service) as unknown) as Interpreter<
    Context,
    Schema,
    Event
  >;

  const [state, send] = useService<Context, Event>(consumedService);
  const canTransition = useMemo(() => canTransitionToService(consumedService), [
    consumedService,
  ]);
  const nextState = useMemo(() => nextStateForService(consumedService), [
    consumedService,
  ]);
  if (!actor) {
    return [undefined, doNothing, returnFalse, returnUndefined] as [
      undefined,
      () => void,
      () => boolean,
      () => undefined,
    ];
  }

  return [
    (state as unknown) as MachineState<Context, Event, Schema>,
    send,
    canTransition,
    nextState,
  ] as [
    MachineState<Context, Event, Schema>,
    Interpreter<Context, Schema, Event>['send'],
    typeof canTransition,
    typeof nextState,
  ];
}
