import styled from 'styled-components';
import {
  Address,
  UserAddress,
  ValidationError,
} from 'sanstatecharts-onboarding';
import Label from './Label';
import Selection from './Selection';
import AddressInput from './AddressInput';
import React, { MutableRefObject } from 'react';
import MonthInput from './MonthInput';

const Container = styled.div``;

type Props = {
  onChange: (a: UserAddress) => void;
  name: string;
  value: UserAddress;
  disabled?: boolean;
  title?: string;
  error: (
    key: keyof UserAddress | keyof Address,
  ) => ValidationError | undefined;
  refFor: (key?: keyof UserAddress | keyof Address) => MutableRefObject<any>;
  children?: React.ReactNode;
};

export default function UserAddressInput({
  onChange,
  name,
  value,
  children,
  title,
  error,
  refFor,
  disabled = false,
}: Props) {
  return (
    <Container>
      <AddressInput
        name={`${name}address`}
        title={title}
        value={value.selectedAddress}
        onChange={(selectedAddress) => onChange({ ...value, selectedAddress })}
        error={(key?: keyof Address) => error(key || 'selectedAddress')}
        refFor={(key?: keyof Address) => refFor(key || 'selectedAddress')}>
        {children}
      </AddressInput>
      <Label
        error={error('accommodationCircumstances')}
        ref={refFor('accommodationCircumstances')}>
        Accommodation circumstances
        <Selection
          options={{
            LIVING_WITH_PARENTS: 'Living with parents',
            LIVING_WITH_FRIENDS: 'Living with friends/relatives',
            TENANT: 'Tenant',
            OUTRIGHT_OWNER: 'Outright owner',
            OWNER_WITH_MORTGAGE: 'Owner with mortgage',
            OWNER_WITH_SANTANDER_MORTGAGE: 'Owner with Santander mortgage',
          }}
          placeholder="Please select..."
          name={`${name}accommodationCircumstances`}
          onChange={(circumstance) =>
            onChange({
              ...value,
              accommodationCircumstances:
                (circumstance as UserAddress['accommodationCircumstances']) ||
                undefined,
            })
          }
          disabled={disabled}
          value={value.accommodationCircumstances}
          error={error('accommodationCircumstances')}
        />
      </Label>
      <Label error={error('movingInDate')} ref={refFor('movingInDate')}>
        When did you move in?
        <Selection
          options={['Less than 3 years ago', 'More than 3 years ago']}
          placeholder="Please select..."
          name={`${name}moveInSelection`}
          onChange={(selection) =>
            onChange({
              ...value,
              movingInDate:
                selection === ''
                  ? undefined
                  : selection === 'More than 3 years ago'
                  ? 'moreThan3Years'
                  : { month: 0, year: 0 },
            })
          }
          disabled={disabled}
          value={
            value.movingInDate === 'moreThan3Years'
              ? 'More than 3 years ago'
              : typeof value.movingInDate === 'object'
              ? 'Less than 3 years ago'
              : undefined
          }
          error={error('movingInDate')}
        />
      </Label>
      {typeof value.movingInDate === 'object' && (
        <Label error={error('movingInDate')}>
          When did you move in?
          <MonthInput
            value={value.movingInDate}
            onChange={(movingInDate) =>
              onChange({
                ...value,
                movingInDate,
              })
            }
            error={error('movingInDate')}
          />
        </Label>
      )}
    </Container>
  );
}
