export const defaultEnvironment = <const>{
  registrationTermsVersion: 49,
  registrationAttribution: {
    productKey: 'PCA',
    channelKey: 'SANUK',
  },
};

type EnvironmentSettings = typeof defaultEnvironment;

let currentSettings: EnvironmentSettings = defaultEnvironment;

export const getEnv = <Key extends keyof EnvironmentSettings>(key: Key) =>
  currentSettings[key];

export function initialiseEnvironment(e: EnvironmentSettings) {
  currentSettings = e;
}
