import { InitialDepositItem } from 'services/application';
import { IncomeDetails, IncomeDetailsSourceCodes } from 'services/customer';

export type Funds = {
  type:
    | undefined
    | 'EMPLOYMENT'
    | 'RETIREMENT'
    | 'PROPERTY'
    | 'INHERITENCE'
    | 'STOCKS_ETC'
    | 'SALE_DIVESTMENT_ASSETS'
    | 'LEGAL_SETTLEMENT'
    | 'VIRTUAL_CURRENCIES'
    | 'GIFT'
    | 'ANOTHER_PERSON_ENTITY'
    | 'SAVINGS'
    | 'STUDENT'
    | 'OTHER';
  amount: undefined | number;
  origin: string | undefined;
};

export const emptyFunds: Readonly<Funds> = {
  type: undefined,
  amount: undefined,
  origin: undefined,
};

const incomeDetailsSourceCodeToContext = (
  code: IncomeDetailsSourceCodes | undefined,
): Funds['type'] => {
  if (code === undefined) return undefined;
  switch (code) {
    case IncomeDetailsSourceCodes.employmentOrBenefits:
      return 'EMPLOYMENT';
    case IncomeDetailsSourceCodes.retirement:
      return 'RETIREMENT';
    case IncomeDetailsSourceCodes.property:
      return 'PROPERTY';
    case IncomeDetailsSourceCodes.inheritence:
      return 'INHERITENCE';
    case IncomeDetailsSourceCodes.stocksEtc:
      return 'STOCKS_ETC';
    case IncomeDetailsSourceCodes.saleDivestmentAssets:
      return 'SALE_DIVESTMENT_ASSETS';
    case IncomeDetailsSourceCodes.legalSettlement:
      return 'LEGAL_SETTLEMENT';
    case IncomeDetailsSourceCodes.virtualCurrencies:
      return 'VIRTUAL_CURRENCIES';
    case IncomeDetailsSourceCodes.gift:
      return 'GIFT';
    case IncomeDetailsSourceCodes.anotherPersonEntity:
      return 'ANOTHER_PERSON_ENTITY';
    case IncomeDetailsSourceCodes.savings:
      return 'SAVINGS';
    case IncomeDetailsSourceCodes.student:
      return 'STUDENT';
    case IncomeDetailsSourceCodes.other:
      return 'OTHER';
    default:
      return undefined;
  }
};

const contextToIncomeDetailsSourceCode = (
  type: Funds['type'],
): IncomeDetailsSourceCodes | undefined => {
  if (type === undefined) return undefined;
  switch (type) {
    case 'EMPLOYMENT':
      return IncomeDetailsSourceCodes.employmentOrBenefits;
    case 'RETIREMENT':
      return IncomeDetailsSourceCodes.retirement;
    case 'PROPERTY':
      return IncomeDetailsSourceCodes.property;
    case 'INHERITENCE':
      return IncomeDetailsSourceCodes.inheritence;
    case 'STOCKS_ETC':
      return IncomeDetailsSourceCodes.stocksEtc;
    case 'SALE_DIVESTMENT_ASSETS':
      return IncomeDetailsSourceCodes.saleDivestmentAssets;
    case 'LEGAL_SETTLEMENT':
      return IncomeDetailsSourceCodes.legalSettlement;
    case 'VIRTUAL_CURRENCIES':
      return IncomeDetailsSourceCodes.virtualCurrencies;
    case 'GIFT':
      return IncomeDetailsSourceCodes.gift;
    case 'ANOTHER_PERSON_ENTITY':
      return IncomeDetailsSourceCodes.anotherPersonEntity;
    case 'SAVINGS':
      return IncomeDetailsSourceCodes.savings;
    case 'STUDENT':
      return IncomeDetailsSourceCodes.student;
    case 'OTHER':
      return IncomeDetailsSourceCodes.other;
    default:
      return undefined;
  }
};

export const incomeDetailsToFunds = (details: IncomeDetails): Funds => ({
  type: incomeDetailsSourceCodeToContext(details.source_code),
  amount: details.monthly_income,
  origin: details.country_code || undefined,
});

export const fundsToIncomeDetails = (details: Funds): IncomeDetails => ({
  source_code: contextToIncomeDetailsSourceCode(details.type),
  monthly_income: details.amount,
  country_code: details.origin || undefined,
});

export const initialDepositToFunds = (details: InitialDepositItem): Funds => ({
  type: incomeDetailsSourceCodeToContext(details.source || undefined),
  amount: details.amount || undefined,
  origin: details.origin || undefined,
});

export const fundsToInitialDeposit = (details: Funds): InitialDepositItem => ({
  source: contextToIncomeDetailsSourceCode(details.type) || null,
  amount: details.amount || null,
  origin: details.origin || null,
});
