import { AddressResponse } from 'services/address';
import { AddressHistoryEntry } from 'services/customer';

export type Address = {
  id: string;
  addressLineOne: string;
  addressLineTwo: string;
  addressLineThree: string;
  city: string;
  postcode: string;
  country: string;
};

export type UserAddress = {
  selectedAddress: string | Address;
  accommodationCircumstances:
    | undefined
    | 'LIVING_WITH_PARENTS'
    | 'LIVING_WITH_FRIENDS'
    | 'TENANT'
    | 'OUTRIGHT_OWNER'
    | 'OWNER_WITH_MORTGAGE'
    | 'OWNER_WITH_SANTANDER_MORTGAGE';
  movingInDate: undefined | 'moreThan3Years' | { month: number; year: number };
};

export const emptyAddress: Readonly<Address> = {
  id: '',
  addressLineOne: '',
  addressLineTwo: '',
  addressLineThree: '',
  city: '',
  postcode: '',
  country: 'GB',
};

export const emptyUserAddress: Readonly<UserAddress> = {
  selectedAddress: { ...emptyAddress },
  accommodationCircumstances: undefined,
  movingInDate: undefined,
};

export const findAddressResponse = (address: AddressHistoryEntry) => (
  addressResponse: AddressResponse,
) => addressResponse.id === address.address_id;

export const addressResponseToAddress = (
  response: Pick<
    AddressResponse,
    | 'id'
    | 'address_line_1'
    | 'address_line_2'
    | 'address_line_3'
    | 'city'
    | 'district_or_county'
    | 'postal_code'
    | 'country_code'
  >,
): Address => ({
  id: response.id,
  addressLineOne: response.address_line_1,
  addressLineTwo: response.address_line_2 || '',
  addressLineThree: response.address_line_3 || '',
  city: response.city || response.district_or_county,
  postcode: response.postal_code,
  country: response.country_code,
});

export const pruneIrrelevantAddresses = (
  addresses: UserAddress[],
  address: UserAddress,
) =>
  addresses.length === 0
    ? [address]
    : addresses[addresses.length - 1]!.movingInDate === 'moreThan3Years'
    ? addresses
    : [...addresses, address];

export const insertExtraAddressIfNeeded = (
  addresses: UserAddress[],
  address: UserAddress,
  index: number,
  originalArray: UserAddress[],
) =>
  index < originalArray.length - 1 || typeof address.movingInDate !== 'object'
    ? [...addresses, address]
    : [...addresses, address, { ...emptyUserAddress }];
