import React, { MutableRefObject } from 'react';
import { Funds, ValidationError } from 'sanstatecharts-onboarding';
import Label from './Label';
import Selection from './Selection';
import MoneyInput from './MoneyInput';
import Radios from './Radios';
import { countriesData } from 'lib/countries';
import CountryInput from './CountryInput';

type Props = {
  onChange: (a: Funds) => void;
  name: string;
  value: Funds;
  disabled?: boolean;
  children?: React.ReactNode;
  error: (key: keyof Funds) => ValidationError | undefined;
  refFor: (key?: keyof Funds) => MutableRefObject<any>;
  title: string;
  amountTitle: string;
  originTitle: string;
};

export default function FundsInput({
  onChange,
  name,
  value,
  children,
  error,
  refFor,
  title,
  amountTitle,
  originTitle,
  disabled = false,
}: Props) {
  return (
    <>
      <Label error={error('type')} ref={refFor('type')}>
        {title}
        <Selection
          options={{
            EMPLOYMENT: 'Employment',
            RETIREMENT: 'Retirement',
            PROPERTY: 'Property',
            INHERITENCE: 'Inheritence',
            STOCKS_ETC: 'Stocks etc',
            SALE_DIVESTMENT_ASSETS: 'Sale divestment assets',
            LEGAL_SETTLEMENT: 'Legal settlement',
            VIRTUAL_CURRENCIES: 'Virtual currencies',
            GIFT: 'Gift',
            ANOTHER_PERSON_ENTITY: 'Another person entity',
            SAVINGS: 'Savings',
            STUDENT: 'Student',
            OTHER: 'Other',
          }}
          placeholder="Please select..."
          name={`${name}type`}
          onChange={(incomeType) =>
            onChange({
              ...value,
              type:
                incomeType === '' ? undefined : (incomeType as Funds['type']),
            })
          }
          disabled={disabled}
          value={value.type}
          error={error('type')}
        />
      </Label>
      <Label error={error('amount')} ref={refFor('amount')}>
        {amountTitle}
        <MoneyInput
          onChange={(amount) =>
            onChange({
              ...value,
              amount,
            })
          }
          disabled={disabled}
          value={value.amount}
          error={error('amount')}
        />
      </Label>
      {children}
      <Label error={error('origin')} ref={refFor('origin')}>
        {originTitle}
        <Radios
          name="originUK"
          options={['Yes', 'No']}
          onChange={(originUK) =>
            onChange({
              ...value,
              origin: originUK === 'Yes' ? countriesData[0]!.ISOcode : '',
            })
          }
          disabled={disabled}
          value={
            value.origin === undefined
              ? undefined
              : value.origin !== countriesData[0]!.ISOcode
              ? 'No'
              : 'Yes'
          }
          error={error('origin')}
        />
      </Label>
      {value.origin !== undefined &&
        value.origin !== countriesData[0]!.ISOcode && (
          <Label error={error('origin')}>
            Where is it from?
            <CountryInput
              name={`${name}country`}
              value={value.origin}
              disabled={disabled}
              onChange={(origin) => onChange({ ...value, origin })}
              error={error('origin')}
              filterOut={[countriesData[0]!.ISOcode]}
            />
          </Label>
        )}
    </>
  );
}
