import { aboutYouValidation, stateMatches } from 'sanstatecharts-onboarding';
import Page from 'components/Page/Page';
import ProgressBar from 'components/ProgressBar/ProgressBar';
import Heading from 'components/Text/Heading';
import { useOnboarding } from 'lib/useOnboarding';
import PrimaryButton from 'components/Button/PrimaryButton';
import PersonUpdateError from 'components/PersonUpdateError/PersonUpdateError';
import ValidationErrors from 'components/ValidationErrors/ValidationErrors';
import { useValidation } from 'lib/useValidation';
import Form from 'components/Form/Form';
import NameFields from 'components/Fields/NameFields';
import OtherNameFields from 'components/Fields/OtherNameFields';
import DOBField from 'components/Fields/DOBField';
import GenderField from 'components/Fields/GenderField';
import NationalityFields from 'components/Fields/NationalityFields';
import TaxStatusFields from 'components/Fields/TaxStatusFields';

export default function AboutYou() {
  const [state, send, canTransition] = useOnboarding();
  const { aboutYou } = state.context;
  const validationErrors = aboutYouValidation(aboutYou);
  const { shownErrors, refFor, onScrollToError } = useValidation(
    validationErrors,
  );
  return (
    <Page>
      <ProgressBar step="aboutYou" title="About you" />
      <Heading>About you</Heading>
      <Form>
        <NameFields
          shownErrors={shownErrors}
          aboutYou={aboutYou}
          send={send}
          canTransition={canTransition}
          refFor={refFor}
        />
        <OtherNameFields
          shownErrors={shownErrors}
          aboutYou={aboutYou}
          send={send}
          canTransition={canTransition}
          refFor={refFor}
        />
        <GenderField
          shownErrors={shownErrors}
          aboutYou={aboutYou}
          send={send}
          canTransition={canTransition}
          refFor={refFor}
        />
        <DOBField
          shownErrors={shownErrors}
          aboutYou={aboutYou}
          send={send}
          canTransition={canTransition}
          refFor={refFor}
        />
        <NationalityFields
          shownErrors={shownErrors}
          aboutYou={aboutYou}
          send={send}
          canTransition={canTransition}
          refFor={refFor}
        />
        <TaxStatusFields
          shownErrors={shownErrors}
          aboutYou={aboutYou}
          send={send}
          canTransition={canTransition}
          refFor={refFor}
        />
        <PersonUpdateError
          enabled={stateMatches(state, 'steps', 'aboutYou', 'updateError')}
        />
        <ValidationErrors
          validationErrors={validationErrors}
          onScrollToError={onScrollToError}
        />
        <PrimaryButton
          type="submit"
          onClick={() => send({ type: 'CONTINUE' })}
          status={
            stateMatches(state, 'steps', 'aboutYou', 'updating')
              ? 'SUBMITTING'
              : !canTransition({ type: 'CONTINUE' })
              ? 'DISABLED'
              : 'ACTIVE'
          }>
          Continue
        </PrimaryButton>
      </Form>
    </Page>
  );
}
